/* Typography
--------------------------------------------- */
body,
button,
input,
select,
optgroup,
textarea {
	color: #404040;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
	font-size: 1rem;
	line-height: 1.5;
}

a,
a:visited {
	color: #003c71;
	text-decoration: none;
	font-weight: 700;
}

p {
	font-weight: 400;
}

h1 {
	font-size: 2.5em;
	font-weight: 500;
	font-family: "Open Sans", Helvetica, Arial, sans-serif;
}

body,
button,
input,
select,
optgroup,
textarea {
	color: #444;
	font-family: "Open Sans", Helvetica, Arial, sans-serif;
	font-weight: 300;
	font-size: 16px;
	font-size: 1rem;
	line-height: 1.8;
	width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	clear: both;
	margin: 0.5em 0;
}

h1 {
	font-size: 2.5em;
	font-weight: 400;
}

h2 {
	font-size: 2em;
}

h3,
h4 {
	font-size: 1.4em;
}

.widget-area h2 {
	font-size: 1.3em;
}

@media only screen and (max-width: 768px) {
	h1 {
		font-size: 2em;
	}
	h2 {
		font-size: 1.5em;
	}
	h3,
	h4 {
		font-size: 1.25em;
	}
}
p {
	margin: 0 0 1.5em;
}

dfn,
cite,
em,
i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", Courier, monospace;
	font-size: 15px;
	font-size: 0.9375rem;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code,
kbd,
tt,
var {
	font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	font-size: 15px;
