}

mark,
ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}

/* Elements
--------------------------------------------- */
body {
	background: #fff;
}

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul,
ol {
	margin: 0 0 1.5em 3em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: 700;
}

dd {
	margin: 0 1.5em 1.5em;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

img {
	height: auto;
	max-width: 100%;
}

figure {
	margin: 1em 0;
}

table {
	margin: 0 0 1.5em;
	width: 100%;
}

/* Links
--------------------------------------------- */
a {
	color: #003c71;
}
a:visited {
	color: #003c71;
}
a:hover, a:focus, a:active {
	color: #191970;
}
a:focus {
	outline: thin dotted;
}
a:hover, a:active {
	outline: 0;
}

/* Forms
--------------------------------------------- */
button,
input[type=button],
input[type=reset],
input[type=submit] {
	border: 1px solid;
	border-color: #ccc #ccc #bbb;
	border-radius: 3px;
	background: #e6e6e6;
	color: rgba(0, 0, 0, 0.8);
	line-height: 1;
	padding: 0.6em 1em 0.4em;
}
button:hover,
input[type=button]:hover,
input[type=reset]:hover,
input[type=submit]:hover {
	border-color: #ccc #bbb #aaa;
}
button:active, button:focus,
input[type=button]:active,
input[type=button]:focus,
input[type=reset]:active,
input[type=reset]:focus,
input[type=submit]:active,
input[type=submit]:focus {
	border-color: #aaa #bbb #bbb;
}

input[type=text],
input[type=email],
input[type=url],
input[type=password],
input[type=search],
input[type=number],
input[type=tel],
input[type=range],
input[type=date],
input[type=month],
input[type=week],
input[type=time],
input[type=datetime],
input[type=datetime-local],
input[type=color],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;
	padding: 3px;
}
input[type=text]:focus,
input[type=email]:focus,
input[type=url]:focus,
input[type=password]:focus,
input[type=search]:focus,
input[type=number]:focus,
input[type=tel]:focus,
input[type=range]:focus,
input[type=date]:focus,
input[type=month]:focus,
input[type=week]:focus,
input[type=time]:focus,
input[type=datetime]:focus,
input[type=datetime-local]:focus,
input[type=color]:focus,
textarea:focus {
	color: #111;
}

select {
	border: 1px solid #ccc;
}

textarea {
	width: 100%;
}

/*--------------------------------------------------------------
# Layouts
--------------------------------------------------------------*/
.site {
	max-width: 2200px;
	margin: 0 auto;
	overflow: hidden;
	display: grid;
	grid-template-columns: 1fr minmax(0, 1000px) 1fr;
	grid-template-areas: "header header header" ". main ." ". sidebar ." "footer footer footer";
}

.site-header {
	grid-area: header;
}

.site-main {
	grid-area: main;
	margin-top: 55px;
	padding: 0 1rem;
}

.widget-area {
	grid-area: sidebar;
}

.site-footer {
	grid-area: footer;
}

.site-footer {
	background-color: #333;
	margin-top: 20px;
	padding: 1rem;
	font-size: 0.875rem;
	color: #fff;
	text-align: center;
}

.site-footer .custom-logo {
	max-width: 150px;
}

.site-footer a {
	color: #fff;
	text-decoration: none;
}

.site-footer a:hover,
.site-footer a:focus,
.site-footer a:active {
	color: #305c9f;
}

.site-footer nav ul {
	list-style-type: none;
	margin: 0;
}

@media screen and (min-width: 62.5em) {
	.site-footer {
		padding: 2rem 1rem;
		text-align: left;
		display: grid;
		grid-template-columns: 1fr repeat(3, minmax(auto, 20.83rem)) 1fr;
	}
	.site-footer .footer-logo {
		grid-column: 2/3;
		margin: 0;
	}
	.site-footer .footer-nav {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
}
/*--------------------------------------------------------------
# Components
--------------------------------------------------------------*/
/* Navigation
--------------------------------------------- */
.main-navigation {
	display: block;
	width: 100%;
}
.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-left: 0;
}
.main-navigation ul ul {
	box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
	float: left;
	position: absolute;
	top: 100%;
	left: -999em;
	z-index: 99999;
}
.main-navigation ul ul ul {
	left: -999em;
	top: 0;
}
.main-navigation ul ul li:hover > ul, .main-navigation ul ul li.focus > ul {
	display: block;
	left: auto;
}
.main-navigation ul ul a {
	width: 200px;
}
.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
	left: auto;
}
.main-navigation li {
	position: relative;
}
.main-navigation a {
	display: block;
	text-decoration: none;
}
/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: block;
}

@media screen and (min-width: 37.5em) {
	.menu-toggle {
		display: none;
	}
	.main-navigation ul {
		display: flex;
	}
}
.site-main .comment-navigation,
.site-main .posts-navigation,
.site-main .post-navigation {
	margin: 0 0 1.5em;
}
.comment-navigation .nav-links,
.posts-navigation .nav-links,
.post-navigation .nav-links {
	display: flex;
}
.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
	flex: 1 0 50%;
}
.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
	text-align: end;
	flex: 1 0 50%;
}

.site-header {
	width: 100%;
	height: 55px;
	background-color: #000000;
	border-top: 3px solid #305c9f;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 1000;
}

.admin-bar .site-header {
	top: 46px;
}

@media screen and (min-width: 48.875em) {
	.admin-bar .site-header {
		top: 32px;
	}
}
.site-header .site-title {
	width: 200px;
	font-size: 1.3em;
	text-transform: uppercase;
	margin: 0.4rem 1rem 0;
}

.site-header .site-title a {
	display: block;
	color: #fff;
}

.site-header .site-title a {
	text-decoration: none;
}

@media screen and (min-width: 50em) {
	.site-header {
		display: grid;
		grid-template-columns: 1fr minmax(auto, 12.5rem) minmax(auto, 50rem) 1fr;
		align-items: center;
	}
	.site-header .site-title {
		grid-column: 2/3;
		margin: 0 1rem 0;
	}
}
.main-navigation {
	display: block;
	width: 100%;
	position: absolute;
	top: 52px;
	right: 0;
	z-index: 1000;
}

.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-left: 0;
	background-color: #000000;
}

.main-navigation li {
	position: relative;
}

.main-navigation li.current-menu-item > a,
.main-navigation li.current_page_item > a,
.main-navigation li:hover > a,
.main-navigation li.focus > a {
	background-color: #305c9f;
}

.main-navigation a {
	padding: 1rem;
	display: block;
	font-size: 0.9em;
	text-decoration: none;
	text-align: center;
	font-weight: 700;
	color: white;
	text-transform: uppercase;
}

.main-navigation.toggled ul {
	display: block;
}

.main-navigation ul ul {
	background-color: #3d3d3d;
}

.menu-toggle {
	display: block;
	width: 46px;
	height: 46px;
	margin: 0;
	padding: 0;
	font-size: 0.9em;
	color: #fff;
	position: absolute;
	right: 1rem;
	z-index: 1100;
	top: -3rem;
	border: none;
	box-shadow: none;
	text-shadow: none;
	text-decoration: none;
	cursor: pointer;
	background: transparent;
}

.menu-toggle svg path {
	fill: currentColor;
}

@media screen and (min-width: 50em) {
	.menu-toggle {
		display: none;
	}
	.main-navigation {
		width: auto;
		position: relative;
		top: 0;
		align-self: center;
		margin-left: auto;
		height: 52px;
		line-height: 52px;
	}
	.main-navigation a {
		padding: 0 1rem;
	}
	.main-navigation ul,
	.main-navigation.toggled ul {
		display: flex;
		justify-content: center;
	}
	.main-navigation ul ul {
		box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
		float: left;
		position: absolute;
		top: 100%;
		left: -999em;
		z-index: 99999;
		background-color: #111;
		flex-direction: column;
	}
	.main-navigation ul ul a {
		left: -999em;
		top: 0;
		width: 200px;
		text-align: left;
	}
	.main-navigation ul ul a:hover {
		background-color: #305c9f;
	}
	.main-navigation ul ul li:hover > ul,
	.main-navigation ul ul li.focus > ul {
		left: 100%;
		top: 0;
	}
	.main-navigation ul li:hover > ul,
	.main-navigation ul li.focus > ul {
		left: auto;
	}
}
/* Posts and pages
--------------------------------------------- */
.sticky {
	display: block;
}

.post,
.page {
	margin: 0 0 1.5em;
}

.updated:not(.published) {
	display: none;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

/* Comments
--------------------------------------------- */
.comment-content a {
	word-wrap: break-word;
}

.bypostauthor {
	display: block;
}

/* Widgets
--------------------------------------------- */
.widget {
	margin: 0 0 1.5em !important;
	/* Make sure select elements fit in widgets. */
}

.widget select {
	max-width: 100%;
}

.widget-area ul {
	list-style-type: square;
	list-style-position: inside;
}

@media only screen and (min-width: 48em) {
	.widget-area {
		display: flex;
		justify-content: space-between;
		gap: 1.6em;
	}
	.widget {
		width: 20%;
	}
}
/* Media
--------------------------------------------- */
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
	display: inline-block;
}

/* Captions
--------------------------------------------- */
.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
}
.wp-caption img[class*=wp-image-] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}
.wp-caption .wp-caption-text {
	margin: 0.8075em 0;
}

.wp-caption-text {
	text-align: center;
}

/* Galleries
--------------------------------------------- */
.gallery {
	margin-bottom: 1.5em;
	display: grid;
	grid-gap: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	width: 100%;
}

.gallery-columns-2 {
	grid-template-columns: repeat(2, 1fr);
}

.gallery-columns-3 {
	grid-template-columns: repeat(3, 1fr);
}

.gallery-columns-4 {
	grid-template-columns: repeat(4, 1fr);
}

.gallery-columns-5 {
	grid-template-columns: repeat(5, 1fr);
}

.gallery-columns-6 {
